// Transitions
// =============================================================================
$speed:  0.3s;
$speed-fast: ($speed * 0.5);
$speed-medium: ($speed * 1.5);
$speed-slow: ($speed * 2);
$speed-medium-slow: ($speed * 2.5);
$speed-slower: ($speed * 3);
$speed-slower-medium: ($speed * 3.5);
$speed-slowest: ($speed * 4);
$easing: cubic-bezier(0.4, 0, 0, 1);
$easingBounce: cubic-bezier(.6,.01,0,1.38);


@keyframes textDiscover {
    0% {
        //opacity: 1
        transform: scale3d(1,0,1);
        transform-origin: center top; }
    40% {
        //opacity: .75
        transform: scale3d(1,1,1);
        transform-origin: center top; }
    60% {
        //opacity: .5
        transform: scale3d(1,1,1);
        transform-origin: center bottom; }
    100% {
        //opacity: 0
        transform: scale3d(1,0,1);
        transform-origin: center bottom; } }

@keyframes blockDiscover {
    0% {
        transform: scale3d(0,1,1);
        transform-origin: left center; }
    40% {
        transform: scale3d(1,1,1);
        transform-origin: left center; }
    60% {
        transform: scale3d(1,1,1);
        transform-origin: right center; }
    100% {
        transform: scale3d(0,1,1);
        transform-origin: right center; } }



.js body {
    .shapeTransit1,
    .shapeTransit2 {
        height: 100%;
        width: 100%;
        bottom: -100%;
        left: 0;
        position: fixed; }
    .shapeTransit1 {
        background: #d8dde6;
        z-index: 20000;
        @include pseudo;
        &::after {
            background-image: url(../img/logo__full.svg);
            background-size: cover;
            position: absolute;
            left: 50%;
            top: 20%;
            display: block;
            transform: translate(-50%, -50%);
            opacity: .5;
            width: 320px;
            height: 100px;
            @include sm {
                width: 250px;
                height: 78px; } } }
    .shapeTransit2 {
        background: #f5f0ec;
        z-index: 21000; }


    > .loader {
        position: fixed;
        left: 0;
        right: 0;
        transition: height $speed $speed $easing;
        width: 100%;
        //height: 100%
        height: 100vh;
        background: #d8dde6;
        overflow: hidden;
        z-index: 999;
        .loader__wrap {
            position: absolute;
            left: 50%;
            top: 50%;
            display: block;
            transform: translate(-50%, -50%);
            @include pseudo;
            &::before {}
            &::after {
                background-image: url(../img/logo__full.svg);
                background-size: cover;
                animation: pulse 0.9s alternate infinite;
                @keyframes pulse {
                  from {
                    transform: scale(1.1); }
                  to {
                    transform: scale(0.8);
                    opacity: 0.5; } } } }
        .loader__wrap {
            width: 320px;
            height: 100px;
            @include sm {
                width: 250px;
                height: 78px; }
            &::before {}
            &::after {
                width: 320px;
                height: 100px;
                @include sm {
                    width: 250px;
                    height: 78px; } } } } }


.js body {
    overflow: hidden;
    &.is-loaded {
        overflow: initial;
        > .loader {
            height: 0; } } }








@mixin anim($class, $transform, $delay: .8s) {
  #{$class} {
    opacity: 0;
    transition: all 320ms ease-out;
    transform: #{$transform};
    //.is-loaded &,
    .is-animate & {
        opacity: 1;
        transform: none;
        transition-delay: #{$delay}; } } }

@mixin anim-items($class, $transform, $animDelay:0.1s, $n:50) {
  @include anim($class, $transform);
  @for $i from 1 through $n {
    //.is-loaded,
    .is-animate {
        #{$class}:nth-child(#{$i}) {
            transition-delay: ($animDelay + ($i - 1) * 0.1s); } } } }

