.noUi-target { touch-action: none; user-select: none; box-sizing: border-box; position: relative; direction: ltr; }

.noUi-target * { touch-action: none; user-select: none; box-sizing: border-box; }

.noUi-base { width: 100%; height: 100%; position: relative; z-index: 1; /* Fix 401 */ }

.noUi-origin { position: absolute; right: 0; top: 0; left: 0; bottom: 0; }

.noUi-handle { position: relative; z-index: 1; }

.noUi-stacking .noUi-handle { z-index: 10; }

.noUi-state-tap .noUi-origin { transition: left 0.3s,top .3s; }

.noUi-state-drag * { cursor: inherit !important; }

/* Painting and performance; Browsers can paint handles in their own layer. */
.noUi-base, .noUi-handle { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

/* Slider size and handle placement; */
.noUi-horizontal { height: 4px; }

.noUi-horizontal .noUi-handle { width: 18px; height: 18px; border-radius: 50%; left: -7px; top: -7px; background-color: #b09c6d; }

/* Styling;*/
.noUi-background { background: #D6D7D9; }

.noUi-connect { background: #b09c6d; transition: background 450ms; }

.noUi-origin { border-radius: 2px; }

.noUi-target { border-radius: 2px; }

/* Handles and cursors;*/
.noUi-draggable { cursor: w-resize; }

.noUi-vertical .noUi-draggable { cursor: n-resize; }

.noUi-handle { cursor: default; box-sizing: content-box !important; }

.noUi-handle:active { border: 8px solid #345DBB; border: 8px solid rgba(53, 93, 187, 0.38); background-clip: padding-box; left: -14px; top: -14px; }

/* Disabled state;*/
[disabled].noUi-connect, [disabled] .noUi-connect { background: #B8B8B8; }

[disabled].noUi-origin, [disabled] .noUi-handle { cursor: not-allowed; }

